@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-VariableFont_wght.ttf");
  font-style: normal;
}
.header {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__logo-img {
  width: 197px;
  height: 32px;
  object-fit: contain;
}
.header__content {
  display: flex;
  align-items: center;
  gap: 24px;
  max-width: 1176px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 12px;
}
.header__links {
  display: flex;
  padding: 0;
  margin: 0 0 0 auto;
  list-style: none;
}
.header__link {
  display: flex;
  border-bottom: 2px solid transparent;
  padding: 12px 24px;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
  color: inherit;
  text-decoration: none;
  transition: border-color 0.2s;
}
.header__link:hover {
  border-bottom-color: rgb(240, 84, 79);
}

.footer {
  display: flex;
  align-items: center;
  min-height: 332px;
  margin-top: auto;
  background-color: rgb(41, 41, 41);
}
.footer__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  max-width: 1176px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 12px;
}
.footer__block {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 32px 0;
}
.footer__logo {
  display: flex;
  align-items: center;
}
.footer__logo-img {
  width: 247px;
  height: 40px;
  object-fit: contain;
}
.footer__copyright {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: rgb(255, 255, 255);
}
.footer__links {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0;
  margin: 0;
  list-style: none;
}
.footer__links-element {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 36px;
}
.footer__links-element::after {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 2px;
  border-radius: 10px;
  background-color: rgb(240, 84, 79);
}
.footer__link {
  color: rgba(255, 255, 255, 0.92);
  text-decoration: none;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 160px;
  padding: 12px 24px;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 2px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.m {
  font-size: 16px;
  line-height: 24px;
}
.button.l {
  font-size: 18px;
  line-height: 28px;
}
.button.primary {
  border-color: rgb(240, 84, 79);
  background-color: rgb(240, 84, 79);
  color: rgba(255, 255, 255, 0.92);
}
.button.primary:hover:not(:disabled) {
  border-color: rgb(144, 50, 47);
  background-color: rgb(144, 50, 47);
}
.button.primary:disabled {
  opacity: 0.48;
}
.button.secondary {
  background-color: rgb(255, 255, 255);
  border-color: rgb(240, 84, 79);
  color: rgb(41, 41, 41);
}
.button.secondary:hover:not(:disabled) {
  background-color: rgb(252, 221, 220);
}
.button.secondary:disabled {
  opacity: 0.48;
}

.promo {
  display: flex;
  flex-direction: column;
  padding: 64px 0 192px;
  background-image: url("../img/graph.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
}
.promo__text {
  position: relative;
  display: flex;
  padding-left: 44px;
  margin: 0 0 32px;
  font-weight: 600;
}
.promo__text::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 32px;
  height: 4px;
  border-radius: 20px;
  background-color: rgb(240, 84, 79);
}

.description {
  width: 100vw;
  padding: 96px 0;
  margin-left: calc((1152px - 100vw) / 2);
  background-color: rgb(240, 84, 79);
}
.description__text {
  max-width: 1176px;
  padding: 0 12px;
  margin: 0 auto;
  font-size: 32px;
  font-weight: 600;
  line-height: 44px;
  color: rgba(255, 255, 255, 0.92);
}

.reports {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 96px 0;
}
.reports__description {
  max-width: 720px;
}

.reports-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 32px;
}
.reports-grid__block {
  display: flex;
  flex-direction: column;
  gap: 64px;
  padding: 32px;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.04);
}
.reports-grid__block:first-child {
  grid-row: 1/3;
}
.reports-grid__block_low {
  gap: 32px;
}
.reports-grid__block_low .reports-grid__title-block {
  padding-bottom: 0;
}
.reports-grid__block_low .reports-grid__title {
  font-size: 24px;
  line-height: 32px;
}
.reports-grid__block_low .reports-grid__prices {
  flex-direction: row;
  gap: 48px;
}
.reports-grid__block_low .reports-grid__price {
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
}
.reports-grid__title-block {
  display: flex;
  padding-bottom: 40px;
}
.reports-grid__title {
  font-size: 32px;
  font-weight: 600;
  line-height: 44px;
}
.reports-grid__prices {
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-top: auto;
}
.reports-grid__prices-block {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.reports-grid__price-text {
  font-size: 16px;
  line-height: 24px;
}
.reports-grid__price {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 48px;
  font-weight: 400;
  line-height: 58px;
}
.reports-grid__price_red {
  color: rgb(240, 84, 79);
}
.reports-grid__price_green {
  color: rgb(50, 159, 91);
}
.reports-grid__price-icon {
  width: 32px;
  height: 32px;
}
.reports-grid__price-icon_revert {
  transform: rotate(180deg);
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  font-size: 20px;
  line-height: 1.6;
  font-weight: 400;
  font-family: "Montserrat", "Helvetica", sans-serif;
  color: rgb(41, 41, 41);
  background-color: rgb(255, 255, 255);
}

.main {
  display: flex;
  flex-direction: column;
  max-width: 1176px;
  min-height: calc(100vh - 432px);
  padding: 0 12px 0;
  margin: 100px auto 0;
}

.heading1 {
  margin: 0 0 48px;
  font-size: 32px;
  font-weight: 600;
  line-height: 44px;
}

.heading2 {
  margin: 0;
  font-size: 32px;
  font-weight: 600;
  line-height: 44px;
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}